.country-card {
	display: flex;
	flex-direction: column;	
	justify-content: center;
	align-items: center;
	width: 100%;
	max-width: 250px;
	background: #fff;
	color: #000;
	padding: 30px 10px;
	margin: 30px auto;
}

img {
	object-fit: contain;
	max-height: 150px;
	max-width: 100px;
}
h4 {
	margin: 10px 0;
	font-size: 25px;
}
p {
	margin: 5px 0;
}